import React, { useState, useLayoutEffect } from "react";
import { Container, Row, Col } from "reactstrap";
import Input from "../common/INPUT";
import { useForm } from "react-hook-form";
import { Link } from "react-router-dom";
import "./Assets/login.scss";
function Login(props) {
  const { register, errors } = useForm({
    mode: "onBlur",
    defaultValues: {},
    validateCriteriaMode: "firstErrorDetected"
  });
  const [showPassword, setShowPassword] = useState("eye");
  const togglePassword = () =>
    setShowPassword(showPassword === "eye" ? "eye-slash" : "eye");
  const onSubmitHandle = e => {
    e.preventDefault();
  };
  const [size, setSize] = useState(window.innerWidth);
  const [width, setWidth] = useState("60%");
  useLayoutEffect(() => {
    function updateSize() {
      setSize(window.innerWidth);
      setWidth(window.innerWidth <= 768 ? "90%" : "60%");
    }
    window.addEventListener("resize", updateSize);
    if (window.innerWidth <= 768) {
      updateSize();
    }
  }, []);
  return (
    <React.Fragment>
      <Container fluid={true} className="LOGIN_CONTAINER">
        <Row>
          <Col className="MOBILE_TITLE d-flex d-md-none">
            <img src="img/Logo.png" className="img-fluid" alt="" />
          </Col>
          <Col md={6} className="COVER_TITLE">
            <img
              src={`img/${
                size <= 786 ? "mobileCover@3x" : "LoginImg@3x"
              }.png`}
              alt=""
            />
          </Col>
          <Col md={6} className="LOGIN_FORM p-md-0 py-3">
            <img src="img/Logo.png" className="d-md-inline d-none" alt="" />
            <form style={{ width }} onSubmit={onSubmitHandle}>
              <Input
                width="100%"
                placeholder="Enter Email Here..."
                label={"E-mail"}
                name="email"
                type="email"
                refs={register({ required: true })}
                img="img/check.png"
                errors={errors.email}
                isError={true}
              />
              <Input
                width="100%"
                placeholder="Enter Password Here..."
                label={"Password"}
                name="password"
                type={showPassword === "eye" ? "password" : "text"}
                refs={register({ required: true })}
                icon={showPassword}
                errors={errors.password}
                isError={true}
                onClickHandle={togglePassword}
              />
              <span>
                {errors.password || errors.email ? "Invalid Credentials" : ""}
              </span>
              <button>Sign In</button>
              <Link to="/password-reset">Forgot your password?</Link>
              <div className="Or">
                <div className="line" />
                <p>OR</p>
                <div className="line" />
              </div>
              <div className="SignUp">
                <p>Don't have an account?</p>
                <Link to="/signup">Register</Link>
              </div>
              <div className="Social">
                <div className="Content">
                  <div className="Icon">
                    <div className="Container">
                      <i className="fa fa-twitter"></i>
                    </div>
                  </div>
                  <div className="Text">
                    <p>Twitter</p>
                  </div>
                </div>
                <div className="Content">
                  <div className="Icon">
                    <div className="Container">
                      <i className="fa fa-facebook"></i>
                    </div>
                  </div>
                  <div className="Text">
                    <p>Facebook</p>
                  </div>
                </div>
                <div className="Content">
                  <div className="Icon">
                    <div className="Container">
                      <i className="fa fa-google"></i>
                    </div>
                  </div>
                  <div className="Text">
                    <p>Google</p>
                  </div>
                </div>
              </div>
            </form>
          </Col>
        </Row>
      </Container>
    </React.Fragment>
  );
}
Login.whyDidYouRender = true;
export default Login;
